import React, { useState, useEffect } from "react";
import "./IngredientsDesktop.scss";
import * as ScrollMagic from "scrollmagic";
import gsap from "gsap";
import HamChorizoIngredients2 from "./images/chorizo-szynka-kukurydza-cebula.png";
import HamChorizoIngredients3 from "./images/pomidor-kukurydza.png";

const IngredientsDesktop = () => {
  // eslint-disable-next-line
  const [scrollMagic, setScrollMagic] = useState({
    controller: new ScrollMagic.Controller(),
    timelineOne: gsap.timeline(),
  });
  const { controller, timelineOne } = scrollMagic;
  useEffect(() => {
    // ingredients animation
    timelineOne.staggerFromTo(
      [".hamChorizoIngredients2", ".hamChorizoIngredients3"],
      0.5,
      { y: "50px", opacity: 0 },
      { y: "0", opacity: 1 },
      0.25
    );

    new ScrollMagic.Scene({
      triggerElement: ".ingredients_desktop2",
      triggerHook: 0.7,
    })
      .setTween(timelineOne)
      .addTo(controller);
    // eslint-disable-next-line
  }, []);
  return (
    <div className="ingredients_desktop2">
      <img
        src={HamChorizoIngredients2}
        alt="Szynka chorizo kukurydza cebula"
        className="hamChorizoIngredients2"
      />
      <img
        src={HamChorizoIngredients3}
        alt="Pomidor kukurydza"
        className="hamChorizoIngredients3"
      />
    </div>
  );
};

export default IngredientsDesktop;
