import React, { useState, useEffect } from "react";
import * as ScrollMagic from "scrollmagic";
import gsap from "gsap";
import HamChorizo from "./HamChorizo/HamChorizo";
import HamChorizoBox from "./images/pizza-ham-chorizo-pudelko.png";
import IngredientsDesktop from "./Ingredients/IngredientsDesktop/IngredientsDesktop";
import "./Main.scss";
import Ingredients from "./Ingredients/Ingredients";
import InstructionDesktop from "../Instruction/InstructionDesktop/InstructionDesktop";
import Informations from "../Informations/Informations";
import useWindowSize from "../../../../Hooks/UseWindowSize";

const Main = () => {
  const size = useWindowSize();
  const { width } = size;
  // eslint-disable-next-line
  const [scrollMagic, setScrollMagic] = useState({
    controller: new ScrollMagic.Controller(),
    timelineOne: gsap.timeline(),
  });
  const { controller, timelineOne } = scrollMagic;
  useEffect(() => {
    // pizzaBox animation
    timelineOne.fromTo(
      ".hamChorizoBox",
      { y: "100px", opacity: 0 },
      { y: "0", opacity: 1 }
    );

    new ScrollMagic.Scene({
      triggerElement: ".hamChorizoBoxBackground",
      triggerHook: 0.95,
    })
      .setTween(timelineOne)
      .addTo(controller);
    // eslint-disable-next-line
  }, []);
  return (
    <>
      <main className="subpageMain">
        <HamChorizo />
        <InstructionDesktop />
        <div className="hamChorizoBoxBackground">
          <div className="hamChorizoBox">
            <img
              src={HamChorizoBox}
              alt="Ham & garlic sauce - pizza z szynka i aromatycznym sosem czosnkowym na puszystym ciescie"
            />
            <div className="palmOilFree">
              <img
                alt="palm oil free"
                src={`${process.env.PUBLIC_URL}/images/palm-oil-free.png`}
              /> 
            </div>
          </div>
          <Ingredients />
          <IngredientsDesktop />
          {width > 1023 ? <Informations /> : null}
        </div>
      </main>
    </>
  );
};

export default Main;
